import Vue from 'vue'
import Router, { RouterOptions } from 'vue-router'
import Home from './views/home/home.vue'

Vue.use(Router);

const scrollBehavior: RouterOptions['scrollBehavior'] = function (to, from, savedPosition) {
  if (to.hash) {
    return {
      selector: to.hash
    };
  } else if (savedPosition) {
    return savedPosition;
  } else {
    return undefined;
  }
}

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
  ],
  scrollBehavior,
})
