



















import {Vue, Component, Prop, Watch} from 'vue-property-decorator';

@Component
export default class ContentTemplate extends Vue {
    @Prop({
        type: String,
        default: '',
    }) private href!: string;

    @Prop({
        type: Boolean,
        default: false,
    }) private route!: boolean;

    @Prop({
        type: Boolean,
        default: false,
    }) private reverse!: boolean;
};
