import {main} from './src/main';
import {VueConstructor} from "vue";
import {Language} from "@/translator/types.d.ts";

const translateTable = [...main];

class Translator {
    private map: Map<string, string> = new Map();
    public label(name: string): string{
        return this.map.get(name) || '';
    }
    constructor() {
        const lang = window.localStorage.getItem('language') || 'en';
        this.language = lang;
        translateTable.forEach((e) => {
            // @ts-ignore
            this.map.set(e.name, e[lang]);
            this._loadedNumber++;
        })
    }

    // Progress
    private _totalNumber: number = translateTable.length;
    public get totalNumber() {
        return this._totalNumber;
    }

    private _loadedNumber: number = 0;
    public get loadedNumber() {
        return this._loadedNumber;
    }

    // Default language
    private _language: Language = 'en';
    public set language(val: string) {
        switch (val) {
            default:
            case 'en':
                this._language = 'en';
                break;
            case 'cn':
                this._language = 'cn';
                break;
        }
        if (this.initialized) {
            window.localStorage.setItem('language', this.language);
            location.reload();
        } else {
            this.initialized = true;
        }

    }

    public get language(): string {
        return this._language
    }

    private initialized: boolean = false;

}


export default {
    install(Vue: VueConstructor) {
        Vue.prototype.$translator = new Translator();
    }
}

