







import {Vue, Component, Prop, Watch} from 'vue-property-decorator';

@Component
export default class Localization extends Vue {
    @Prop({
        type: Boolean,
        default: false,
    }) private dark!: boolean;

    private changeLang() {
        this.$confirm(<string>this.$translator.label('change_language'), <string>this.$translator.label('notice'), {
            confirmButtonText: <string>this.$translator.label('confirm'),
            cancelButtonText: <string>this.$translator.label('cancel'),
            type: 'warning'
        }).then(() => {
            this.$translator.language = this.$translator.language === 'cn' ? 'en' : 'cn';
        });
    }
};
