import { VueConstructor } from 'vue';

class ScrollHelp {
    /**
     * Scroll to target element if exist
     * @param targetID without "#"
     */
    public to(targetID: string): void {
        const target = document.getElementById(targetID);
        console.log(target);
        if(target) {
            let offset: number = 0;
            let t = target || document.body;
            while(t !== document.body) {
                offset += t.offsetTop;
                t = t.parentElement || document.body;
            }
            window.scrollTo({
                top: offset,
                left: 0,
                behavior: 'smooth',
            });
        }
    }
}

export default {
    install(Vue: VueConstructor) {
        Vue.prototype.$scrollHelper = new ScrollHelp();
    }
};