







import {Vue, Component, Prop, Watch} from 'vue-property-decorator';

@Component
export default class NavItem extends Vue {
    @Prop({
        type: Boolean,
        default: false,
    }) private dark!: boolean;

    @Prop({
        type: String,
        default: 'javascript:void(0);',
    }) private href!: string;

    /**
     * If true, treat href as anchor id, more prior than route
     */
    @Prop({
        type: Boolean,
        default: false,
    }) private anchor!: boolean;

    /**
     * If true, treat href as router path
     * */
    @Prop({
        type: Boolean,
        default: false,
    }) private route!: boolean;

    private click():void {
        if (this.anchor) {
            this.$scrollHelper.to(this.href);
        } else if (this.route && this.$router) {
            this.$router.push(this.href);
        } else {
            (<HTMLElement>this.$refs.a).click();
        }
    }
};
